<template>
  <div>{{json}}</div>
</template>
<script>
  export default {
    data(){
      return {
        json: ''
      }
    },
    mounted() {
      let state = this.$route.params.state;
      let url = this.$route.params.url;
      url = decodeURIComponent(url)
      this.json = '{"state": "'+state+'", "url":"'+ url +'"}'
    }
  }
</script>



